<template lang="pug">
    .programme__content
        li.programmes__point.names
            .programmes__point-number #
            .programmes__point-name Название пункта
            .programmes__point-desc Описание
            .programmes__point-duration-names Часы
            .programmes__point-tools 
        li.programmes__point(v-for="point,pidx in programmePoints" :key="point.id")
            programme-point-item(
                :point='point'
                :index='pidx'
                :programmeId='programme.id'
            )
        new-point(
            v-on:closeNewPoint='closeNewPoint'
            v-on:addNewPoint='addNewPoint'
            v-if='newPointVisible'
            :programmeId='programme.id'
            )
        .buttons__programme
            button(@click.prevent='activateExistedProgramme()' v-if='!newPointVisible&&user.role.role=="ROLE_ADMIN"&&programme.status==="NOT_ACTIVE"').activate__programme-button Активировать программу
            .activate__programme-button-hide
            .buttons__programme-collection
                button(@click.prevent='addPoint()' v-if='!newPointVisible').add__programme-point Добавить пункт
                button(@click='deleteProgramme()').remove__programme-point Удалить программу


</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        programmePointItem: () => import("./programme-point-item"),
        newPoint: () => import("./new-point")
    },
    created() {
        this.fetchProgrammePoints(this.programme.id)
    },
    data() {
        return {
            newPointVisible: false
        }
    },
    computed: {
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("programmePoints", {
            programmePoints: state => state.programmePoints})
    },
    props: {
        programme: Object,
        index: Number
    },
    methods: {
        ...mapActions("programmePoints", ["fetchProgrammePoints"]),
        ...mapActions("programmes", ["removeProgramme", "activateProgramme"]),
        closeNewPoint() {
            this.newPointVisible = !this.newPointVisible
        },
        addPoint(){
            this.newPointVisible=!this.newPointVisible
        },
        addNewPoint(){
            this.newPointVisible=!this.newPointVisible
        },
        deleteProgramme(){
            this.removeProgramme(this.programme.id)
        },
        activateExistedProgramme(){
            this.activateProgramme(this.programme.id)
        }
    }
}
</script>
